import React from 'react';
import Modal from 'react-modal';
import merge from 'lodash/merge'
import { IoIosClose } from "react-icons/io";

import Box from './Box'
import theme from './ThemeProvider/theme'
import Button from './Button';

const defaultStyles = {
  overlay: {
    zIndex: theme.zOrder[3],
    backgroundColor: 'rgba(0,0,0,0.7)'
  },
  content: {
    width: '90%',
    maxWidth: '25em',
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    borderRadius: '1em',
    border: 'none',
    padding: '0',
    backgroundColor: 'white',
    overflow: 'unset',
  }
};

Modal.setAppElement('#___gatsby')

export default ({ children, border, borderRadius, customStyles, py, closeIcon, closeModal,  ...props }) => (
  <Modal
    style={merge({}, defaultStyles, { content: customStyles})}
    onRequestClose={closeModal}
    {...props}
  >
    {closeIcon &&  (
      <Box position="absolute" right="0.5em" top="0.25em">
        <Button.transparent px="0" py="0" onClick={closeModal}><IoIosClose size="2em" /></Button.transparent>
      </Box>
    )}
    <Box py={py ? py : '2em'} px="2em">
      {children}
    </Box>
  </Modal>
)
