import React, { useState, PureComponent } from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { StaticQuery, graphql, withPrefix } from 'gatsby'
import format from 'date-fns/format'
import { IoIosClose, IoIosArrowRoundUp, IoIosArrowRoundDown } from 'react-icons/io';

import Box from 'components/Box';
import Modal from 'components/Modal';
import Button from 'components/Button';
import Flex from 'components/Flex';
import Link from 'components/Link';
import Text from 'components/Text';
import Image from 'components/Image';
import theme from 'components/ThemeProvider/theme';
import isInApp from 'components/utils/isInApp';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import menudoc from './menudoc.svg'
import Header from '../Header'

const menus =[
  {
    label: '看診好幫手',
    to: '/'
  },
  {
    label: '我的看診備忘錄',
    to: '/memo'
  },
  {
    label: '看診中注意事項',
    to: '/notice'
  },
  {
    label: '檢測你的看診表現',
    to: '/test/start'
  },
]

const MobileApp = ({ children, top, bottom, right, left, transform, justifyContent, alignItems, ...props}) => (
  <Box {...props}>
    <Box
      position="fixed"
      top="0"
      bottom="0"
      left="0"
      right="0"
      bg="rgba(0,0,0,0.8)"
      zOrder={3}
    />
    <Box
      position="absolute"
      fontSize="2em"
      color="white"
      top={top}
      left={left}
      right={right}
      bottom={bottom}
      transform={transform}
      zOrder={3}
    >
      <Flex flexDirection={bottom ? 'column' : 'column-reverse'} alignItems={alignItems}>
        <Box mx="1em" fontSize={mobileOrDesktop('0.875em', '1em')}>
          <Text>請用外部瀏覽器開啟</Text>
          <Text>以獲得最佳體驗</Text>
        </Box>
        {children}
      </Flex>
    </Box>
  </Box>
)

class Layout extends PureComponent {
  componentDidMount() {
    if (isInApp) {
      document.body.style.position = 'fixed'
      document.body.style.height = '100vh'
      document.body.style.overflow = 'hidden'
    }
  }

  render() {
    const { children, noHeader, noFooter, intl, headerBg, ...props } = this.props
    return (
      <StaticQuery
        query={graphql`
          query SiteTitleQuery {
            site {
              siteMetadata {
                title
                description
                url
              }
            }
          }
        `}
        render={({ site: { siteMetadata } }) => {
          const title = siteMetadata.title;
          const description = siteMetadata.description;
          const [currentState, setState] = useState(0)
          const [currentOpen, setOpen] = useState(0)
          return (
            <div>
              <Helmet>
                <html lang="zh-Hant-TW" />
                <title>{title}</title>
                <meta name="description" content={description} />
                <link rel="apple-touch-icon" sizes="180x180" href={withPrefix('/apple-touch-icon.png')} />
                <link rel="icon" type="image/png" sizes="32x32" href={withPrefix('/favicon-32x32.png')} />
                <link rel="icon" type="image/png" sizes="16x16" href={withPrefix('/favicon-16x16.png')} />
                <link rel="mask-icon" color="#5bbad" href={withPrefix('/safari-pinned-tab.svg')} />
                <meta name="msapplication-TileColor" content="#da532c" />
                <meta name="theme-color" content="#ffffff" />
                <meta property="og:url" content={siteMetadata.url} />
                <meta property="og:title" content={title} />
                <meta property="og:description" content={description} />
                <meta property="og:image" content={`${siteMetadata.url}/fb.png`} />
              </Helmet>
              {!noHeader && (
                <Header
                  height={theme.headerHeight}
                  siteTitle={title}
                  menuClick={() => Boolean(currentState) ? setState(0) : setState(1)}
                  currentState={currentState}
                  setState={setState}
                  bg={headerBg}
                />)}
              <Box minHeight="100vh" pt={!noHeader && theme.headerHeight} pb={!noFooter && theme.footerHeight} {...props}>
                {children}
              </Box>
              <Flex
                mt={`-${theme.footerHeight}`}
                height={theme.footerHeight}
                bg="titleBlue"
                alignItems="center"
                justifyContent="center"
                position="relative"
              >
                <Button.transparent
                  py="0"
                  color="white"
                  hoverColor="white"
                  fontSize="0.8em"
                  onClick={() => setOpen(1)}
                >
                  網站聲明
                </Button.transparent>
                <Text color="white" fontSize="0.8em">
                  Copyright © {format(new Date(), 'YYYY')}. All rights reserved.
                </Text>
              </Flex>
              <Modal isOpen={currentOpen} closeModal={() => setOpen(0)} closeIcon>
                <Text>
                  本網頁由 Re-lab 及台灣拜耳共同開發及藥師公會全聯會指導，本系統不會蒐集或儲存您所輸入的任何資料，惟為提供您適當的服務，您的資料將會透過功能性 cookies 被蒐集/儲存於您的瀏覽器上，此系統僅供看診準備參考。如您懷疑有副作用而需要協助，請您儘速連絡您的醫師或藥師，以取得適當的醫療協助；或請至台灣拜耳官網的聯絡我們頁面，依照最下方說明處理流程進行相關通報：
                </Text>
                <Button.transparent px="0" py="0" is={Link} href="http://www.bayer.com.tw/zh/contact.php">http://www.bayer.com.tw/zh/contact.php</Button.transparent>
              </Modal>
              {isInApp && (isInApp.name === "Line" || isInApp.name === "WeChat" || isInApp.os !== "iOS") && (
                <MobileApp top="0" right="0.75em" alignItems="flex-end">
                  <IoIosArrowRoundUp size="3em" />
                </MobileApp>
              )}
              {isInApp && isInApp.os === "iOS" && isInApp.name === "Instagram" && (
                <MobileApp bottom="0.25em" left="0" right="0" alignItems="center" >
                  <IoIosArrowRoundDown size="3em" />
                </MobileApp>
              )}
              {isInApp && isInApp.os === "iOS" && isInApp.name === "Facebook" && (
                <MobileApp bottom="0.25em" left="0" right="0" alignItems="flex-end" >
                  <IoIosArrowRoundDown size="3em" />
                </MobileApp>
              )}
              <Box
                bg="bgOrange"
                position="fixed"
                top="0"
                bottom="0"
                transform={Boolean(currentState) ? 'translate(0)' : 'translateX(-120%)'}
                transition="transform 0.5s"
                overflow="hidden"
                zOrder={3}
              >
                <Box transform={`translateY(-${theme.headerHeight})`} pt={theme.headerHeight} ml="1em">
                  <Button.transparent px="0" py="0" color="titleBlue" onClick={() => setState(0)}>
                    <IoIosClose size="3em" />
                  </Button.transparent>
                </Box>
                <Box position="absolute" left="0" bottom="0" transform={['translateY(25%)', null, null, 'translateY(20%)', null, 'translateY(10%)', 'translateY(0)']} width="90%">
                  <Image src={menudoc} />
                </Box>
                <Box bg="bgOrange" position="relative" transform={`translateY(-${theme.headerHeight})`}>
                  {menus.map(({ label, to }) => (
                    <Box pt="1em" key={label}>
                      <Button.menu textAlign="left" width={1} is={Link} to={to}>
                        {label}
                      </Button.menu>
                    </Box>
                  ))}
                </Box>
              </Box>
            </div>
          );
        }}
      />
    )
  }
}


Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
